import MarkdownIt from "markdown-it";

import markdownItAttrs from "markdown-it-attrs";

const htmlMarkdown = new MarkdownIt({
  html: true,
  breaks: true,
  linkify: true
}).use(markdownItAttrs, {
  leftDelimiter: "{{",
  rightDelimiter: "}}",
  allowedAttributes: [ "target", "rel", "class", "id" ]
});

const noHtmlMarkdown = new MarkdownIt({
  html: false,
  breaks: true,
  linkify: true
}).use(markdownItAttrs, {
  leftDelimiter: "{{",
  rightDelimiter: "}}",
  allowedAttributes: [ "target", "rel", "class", "id" ]
});

function parseMarkdown(fullText, allowHtml = true) {
  return allowHtml ? htmlMarkdown.parse(fullText, {}) : noHtmlMarkdown.parse(fullText, {});
}

export { parseMarkdown };
